<template>
	<div class="container-sm" style="padding: 4rem 0;">
		<h1 style="text-align: center;">Sign in!</h1>
		<a-form :style="{ maxWidth: '25rem', margin: '1rem auto' }" :form="signInForm" @submit.prevent="submitForm">
			<a-row :gutter="[30, 20]">
				<a-col>
					<a-form-item>
						<a-input v-decorator="['username', { rules: rules.username }]" placeholder="mail@example.com" />
					</a-form-item>
				</a-col>
				<a-col>
					<a-form-item>
						<a-input-password
							v-decorator="['password', { rules: rules.password }]"
							placeholder="********"
						/>
					</a-form-item>
				</a-col>
				<a-col>
					<a-form-item>
						<a-button icon="" html-type="submit" type="primary">Sign In</a-button>
						<a-button type="link">Don't have an account?</a-button>
					</a-form-item>
				</a-col>
			</a-row>
		</a-form>
		<img
			:style="{ width: '100%', display: 'block', maxWidth: '50rem', margin: '0 auto' }"
			src="images/helper/pablo-sign-in.png"
			alt="Sign In Cards For Women"
		/>
		<a-alert
			v-if="formStatus.message"
			:style="{ marginTop: '2rem' }"
			show-icon
			:type="formStatus.status"
			:message="formStatus.message"
		/>
	</div>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
	data() {
		return {
			signInForm: this.$form.createForm(this, { name: "signInForm" }),
			loggedIn: false,
			formStatus: {
				message: null,
				status: null
			},
			isLoading: false,
			rules: {
				username: [
					{ required: true, message: "This field is required!" },
					{ type: "email", message: "This is not an email" }
				],
				password: [{ required: true, message: "Forgot your password?" }]
			}
		};
	},
	computed: mapState(["isAuthenticated"]),
	methods: {
		...mapActions(["updateAuthStatus"]),
		submitForm() {
			this.isLoading = true;

			this.signInForm.validateFields((err, values) => {
				if (!err) {
					this.$client
						.login({
							email: values.username,
							password: values.password,
							persist: true
						})
						.then((res) => {
							window.localStorage.setItem("token", res.data.token);
							this.updateAuthStatus(true);

							this.$router.push({ name: "account" });
						})
						.catch((error) => {
							this.formStatus.status = "error";
							this.formStatus.message = error.message;
							this.resetErrors();
						})
						.finally(() => {
							this.isLoading = false;
						});
				} else {
					this.isLoading = false;
					this.resetErrors();
				}
			});
		},
		resetErrors() {
			setTimeout(() => {
				// this.signInForm.resetFields();
				this.formStatus.message = null;
				this.formStatus.status = null;
			}, 4000);
		}
	}
};
</script>
